import React, {useEffect, useState} from 'react'

const Index = (props) => {
    const settingsDefault = {
        src: 'https://allods.mail.ru/media/originals/5fca168ceae6e.jpg',
        partsX: 10,
        partsY: 5,
        precision: 1,
        isFinished: false
    }
    const [content, setContent] = useState(null)
    const [settings, setSettings] = useState(settingsDefault)

    const Puzzle = {
        elements: [],
        setElements: function (config = settings) {
            for (let y = 0; y < config.partsY; y++) {
                this.elements[y] = new Array()
                for (let x = 0; x < config.partsX; x++) {
                    const minMax = {
                        top: {
                            min: - y*100/config.partsY,
                            max: 100 - (y+1)*100/config.partsY
                        },
                        left: {
                            min: - x*100/config.partsX,
                            max: 100 - (x+1)*100/config.partsX
                        }
                    }
                    let top = Math.round(minMax.top.min + Math.random() * (minMax.top.max - minMax.top.min)) + '%'    // .5*Math.random()*100 + '%'
                    let left =  Math.round(minMax.left.min + Math.random() * (minMax.left.max - minMax.left.min)) + '%'
                    console.log(top, left, minMax)
                    this.elements[y] = [...this.elements[y],
                        {
                            status: false,
                            element:
                                <img
                                    key={`${y}/${x}`}
                                    src={settings.src}
                                    alt={`Image content error ${y}/${x}`}
                                    draggable={false}
                                    style={{
                                        position: 'absolute',
                                        display: 'block',
                                        top: top,
                                        left: left,
                                        maxHeight: '100%',
                                        maxWidth: '100%',
                                        zIndex: 0,
                                        clipPath: `polygon(${x * 100 / config.partsX}% ${y * 100 / config.partsY}%, ${(x + 1) * 100 / config.partsX}% ${y * 100 / config.partsY}%, ${(x + 1) * 100 / config.partsX}% ${(y + 1) * 100 / config.partsY}%, ${x * 100 / config.partsX}% ${(y + 1) * 100 / config.partsY}%)`
                                    }}
                                    onDragStart={e => { e.preventDefault() }}
                                    onLoad={ev => {
                                        const onClick = (e) => {
                                            ev.target.removeEventListener('mousedown', onClick)
                                            const item = e.currentTarget
                                            item.style.zIndex++

                                            const shiftX = e.currentTarget.clientWidth * (x + .5) / config.partsX
                                            const shiftY = e.currentTarget.clientHeight * (y + .5) / config.partsY

                                            const moveAt = (currentX, currentY) => {
                                                const posX = currentX - shiftX
                                                const posY = currentY - shiftY
                                                item.style.left = posX + 'px'
                                                item.style.top = posY + 'px'
                                                return {x: posX, y: posY}
                                            }
                                            Puzzle.updateElementStatus([y,x], false)

                                            const onMouseMove = event => {
                                                moveAt(event.clientX, event.clientY)
                                            }
                                            const onMouseUp = event => {
                                                item.style.zIndex--
                                                const values = {
                                                    element: {
                                                        x: event.clientX - shiftX,
                                                        y: event.clientY - shiftY
                                                    },
                                                    precision: {
                                                        x: .5*event.target.clientWidth/config.partsX * config.precision,
                                                        y: .5*event.target.clientHeight/config.partsY * config.precision
                                                    },
                                                    getElement: function () {
                                                        const summa = this.element.x*this.element.x + this.element.y*this.element.y
                                                        return Math.sqrt(summa)
                                                    },
                                                    getPrecision: function () {
                                                        const summa = this.precision.x*this.precision.x + this.precision.y*this.precision.y
                                                        return Math.sqrt(summa)
                                                    }
                                                }
                                                if (values.getElement() <= values.getPrecision()) {
                                                    const posX = (x + .5)*event.target.clientWidth/config.partsX
                                                    const posY = (y + .5)*event.target.clientHeight/config.partsY
                                                    moveAt(posX, posY)
                                                    this.updateElementStatus([y,x], true)
                                                }
                                                document.removeEventListener('mousemove', onMouseMove)
                                                document.removeEventListener('mouseup', onMouseUp)
                                                ev.target.addEventListener('mousedown', onClick)
                                            }

                                            document.addEventListener('mousemove', onMouseMove)
                                            document.addEventListener('mouseup', onMouseUp)
                                        }
                                        ev.target.addEventListener('mousedown', onClick)
                                    }}
                                />
                        }
                    ]
                }
            }
        },
        get: function () {
            return this.elements.map(e => e.map(el => el.element))
        },
        updateElementStatus: function (pos, value) {
            this.elements[pos[0]][pos[1]] = {
                ...this.elements[pos[0]][pos[1]],
                status: value
            }
            let result = true
            for (let y = 0; y < this.elements.length; y++) {
                for (let x = 0; x < this.elements[y].length; x++) {
                    if (this.elements[y][x].status===false) { result = false }
                }
            }
            if (result) {
               this.setPassed()
            }
        },
        setPassed: function () {
            const element = <img
                src={settings.src}
                alt={'Image content error'}
                style={{
                    maxHeight: '100%',
                    maxWidth: '100%'
                }}
            />
            setContent(element)
            settings.callback && settings.callback()
        }
    }

    // Puzzle.setElements()
    useEffect(() => {
        setSettings({...settings, ...props.settings})
    }, [props])
    useEffect(() => {
        if (settings.isFinished) {
            Puzzle.setPassed()
        } else {
            Puzzle.setElements(settings)
            setContent(Puzzle.get())
        }
    }, [settings])

    return (
        <div style={{maxHeight:'inherit', maxWidth:'inherit'}}>
            {content}
        </div>
    )
}

export default Index